.sidebar{
    flex:3;   
    margin-right:15px;
    height: calc(100vh - 50px); 
    overflow-y: scroll;
    position: sticky;
    top: 50px;

}
/*這邊是用來處理scrollbar的樣式 track是固定的條  thumb是滑動條 */ 
::-webkit-scrollbar {
    width:5px;
}
::-webkit-scrollbar-track {
    background-color: #f1f1f1;
}
::-webkit-scrollbar-thumb {
    background-color: rgb(179,179,179);
}
.sidebarWrapper{
    padding:20px;
}

.sidebarList{
    padding:0;
    margin:0;
    /* 不需要點點 */
    list-style: none; 
}
.sidebarListItem{
    display: flex;
    align-items: center;
    margin-bottom:20px;
}
.sidebarIcon{
    margin-right:20px;
}

.sidebarButton{
    width:150px;
    border:none;
    padding:10px;
    border-radius: 5px;
    font-weight: 500;
}

.sidebarHr{
    margin:20px 0;

}
.sidebarFriendList{
    padding: 0;
    margin: 0;
    list-style:none;

}
