.messenger{
    height: calc(100vh - 70px);
    display: flex;
}
.chatMenu{
    flex: 3.5;
}
.chatMenuInput{
    width: 90%;
    padding:10px 0;
    border: none;
    border-bottom: 1px solid gray;
}
.chatBox{
    flex: 5.5;
}
.chatBoxWrapper{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.chatBoxTop{
    height: 100%;
    overflow-y: scroll;
    padding-right: 10px;
}
.chatBoxBottom{
    margin-top: 5px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.chatMessageInput{
    width: 80%;
    height: 90px;
    padding: 10px;
}
.chatSubmitButton{
    width: 70px;
    height: 40px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    background-color: teal;
    color: white;
}
.chatOnline{
    flex: 3;
}

.chatMenuWrapper,.chatBoxWrapper,.chatOnlineWrapper{
    padding:10px;
    height:100%
}

.noConversationText{
    position: absolute;
    top: 20%;
    font-size: 50px;
    color: rgb(224, 220,220);
    text-align: center;
    cursor: default;
}
.rightbarTitle{
    margin-bottom: 20px;
}

.chatSidebarTitle{
    margin-top:20px;
    margin-bottom:20px;
    margin-left:10px;
}